@media (max-width: 500px) {
  .Forms_pic {
    display: none;
  }
  .Forms_title {
    margin-top: 100px;
  }
  .form-control {
    margin-bottom: 15px;
  }
  .custom-select {
    margin-bottom: 10px;
  }
  .Form__select {
    margin-left: 0;
    font-size: 13px;
  }
  .Form__span {
    font-size: 13px;
  }
}
.form__main {
  margin-left: 100px;
  margin-right: 100px;
}

/* mobile */
@media screen and (max-width: 480px) {
  .form__main {
    margin-left: 35px;
    margin-right: 35px;
  }
  .y7 {
    margin-top: 50px;
    text-align: center;
    margin-bottom: 50px;
  }
}
